import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Hello from '../views/Hello.vue'

Vue.use(VueRouter)
function dynamicPropsFn(route) {
  return {
    active_tab: parseInt(route.params.years)
  }
}

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('../views/About.vue')
  },
  {
    path: '/conditions',
    name: 'Trail Conditions',
    component: () => import('../views/Conditions.vue')
  },
  {
    path: '/covid_form',
    name: 'COVID-19 Contact Tracing',
    component: () => import('../views/CovidForm.vue')
  },
  {
    path: '/covid_form',
    name: 'COVID-19 Contact Tracing',
    component: () => import('../views/CovidForm.vue')
  },
  {
    path: '/covid_safety',
    name: 'COVID-19 Safety Measures',
    component: () => import('../views/CovidDetails.vue')
  },
  {
    path: '/press',
    name: 'Press Release',
    component: () => import('../views/PressRelease.vue')
  },
  {
    path: '/winter_challenge',
    name: 'Winter Challenge ',
    component: () => import('../views/WinterChallenge.vue')
  },

  {
    path: '/trails',
    name: 'Trails',
    component: () => import('../views/Trails.vue')
  },
  {
    path: '/foodndrink',
    name: 'Food & Drink',
    component: () => import('../views/FoodAndDrink.vue')
  },
  {
    path: '/lodging',
    name: 'Lodging & Camping',
    component: () => import('../views/Lodging.vue')
  },
  {
    path: '/visit/:years',
    name: 'Visit / Rentals',
    component: () => import('../views/Visit.vue'),
    props: dynamicPropsFn
  },
  {
    path: '/visit',
    name: 'Visit / Rentals',
    component: () => import('../views/Visit.vue'),
    props: { active_tab: -1 }
  },
  {
    path: '/visit_confirmation',
    name: 'Visit Confirmed!',
    component: () => import('../views/VisitConfirmation.vue'),
  },
  {
    path: '/rental_confirmation',
    name: 'Rental Confirmed!',
    component: () => import('../views/RentalConfirmation.vue'),
  },
  {
    path: '/thankyou',
    name: 'Thank You',
    component: () => import('../views/ThankYou.vue'),
  },
  {
    path: '/people',
    name: 'About Us',
    component: () => import('../views/BoardMembers.vue'),
  },
  { path: '/static', component: Hello, props: { name: 'world' } },
  { path: '/dynamic/:years', component: Hello, props: dynamicPropsFn },
  { path: '/:pathMatch(.*)*', component: () => import('../views/PageNotFound.vue'), },
  { path: '*', component: () => import('../views/PageNotFound.vue'), },
]

const router = new VueRouter({
  routes, 
  scrollBehavior() {
    return { x: 0, y: 0 }
  },
})

export default router

