<template>
  <v-dialog
    :value="active"
    overlay-color="primary"
    overlay-opacity=".9"
    @click:outside="$emit('close')"
  >
    <v-list color="primary" nav tile elevation="0">
      <v-list-item>
        <v-spacer></v-spacer>
        <v-icon @click="$emit('close')" color="white">mdi-close</v-icon>
      </v-list-item>
      <v-list-item
        v-for="item in tabs"
        :key="item.name"
        @click="clickHandler(item)"
        active-class="test"
        class="text-center"
      >
        <v-list-item-content>
          <v-icon color="white">{{ item.icon }}</v-icon>
          <v-list-item-title class="test"
            ><h2>{{ item.name }}</h2></v-list-item-title
          >
          <v-list v-if="item.expanded" color="primary" nav tile elevation="0">
            <v-list-item
              @click="clickHandler(option)"
              v-for="option in item.options"
              :key="option.name"
              class="text-center"
            >
              <v-list-item-content>
                <h4 style="color: white">{{ option.name }}</h4>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-list-item-content>
      </v-list-item>

      <v-list-item
        href="https://blueberryhillinn.com"
        target="_blank"
        @click="$emit('close')"
        active-class="test"
        class="text-center"
      >
        <v-list-item-content>
          <v-img
            class="center"
            src="@/assets/bbh_logo.png"
            max-width="40"
            transition="scale-transition"
          />
          <v-list-item-title class="test"
            ><h2>Blueberry Hill Inn</h2></v-list-item-title
          >
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-dialog>
</template>

<script>
export default {
  data: () => ({
    tabs: [
      {
        index: 0,
        name: "Home",
        icon: "mdi-home",
        link: "/",
      },
      {
        index: 1,
        name: "COVID-19",
        icon: "mdi-alert",
        expanded: false,
        options: [
          { name: "Safety Details", link: "/covid_safety" },
          { name: "Contact Tracing Form", link: "/covid_form" },
        ],
      },
      {
        index: 2,
        name: "Trails",
        icon: "mdi-hiking",
        expanded: false,
        options: [
          { name: "Conditions", link: "/conditions" },
          { name: "Descriptions", link: "/trails" },
        ],
      },
      {
        index: 3,
        name: "Activities & Rentals",
        icon: "mdi-ski-cross-country",
        link: "/visit",
      },
      {
        index: 4,
        name: "Food & Lodging",
        icon: "mdi-tent",
        link: "/lodging",
      },
      // {
      //   index: 5,
      //   name: "People",
      //   icon: "mdi-account",
      //   link: "/people",
      // },
    ],
  }),
  props: {
    active: Boolean,
  },
  methods: {
    clickHandler: function (i) {
      if (i.options) {
        i.expanded = !i.expanded;
        console.log(i.options);
      } else {
        this.$router.push(i.link);
        this.$emit("close");
      }
    },
  },
};
</script>

<style scoped>
.test {
  color: white;
}
.center {
  margin: auto;
  width: 50%;
  padding: 10px;
}
</style>
